import React, { Component } from 'react'
import { Buffer } from 'buffer'
import $ from 'jquery'

export class DogShow extends Component {
  render () {
    return this.showDachshund()
  }

  printDachshund (data: { items: string | any[] }) {
    const rnd: number = Math.floor(Math.random() * data.items.length)
    const imageSrc = data.items[rnd].media.m.replace('_m', '_b')
    const imageUrl: URL = (new URL(imageSrc))
    if (!['https:'].includes(imageUrl.protocol)) {
      throw new Error('Api response is not https')
    }

    // @ts-ignore
    return <div className="h-100 row align-items-center"><div className="col align-top-side"><img src={imageUrl.toString()} fetchpriority="high" alt="dachshund"/></div>
    </div>
  }

  showDachshund () {
    function generateAuthToken () : string {
      // Yes, this is just for test.
      const userPass = Buffer.from('test:elek').toString('base64')
      return `Basic ${userPass}`
    }

    $.ajaxSetup({
      headers: {
        Authorization: generateAuthToken()
      }
    })

    let jsonResponse

    $.getJSON('https://dogs.simongergely.eu/dogs', {}, function (result) {
      jsonResponse = result
    })
    // @ts-ignore
    return this.printDachshund(jsonResponse)
  }
}
